import classes from './App.module.css';
import { useState } from 'react';
import Form from './components/Form/form';
import validator from 'validator';



function App() {

  const [originalUrl, setOriginalUrl] = useState("");
  const [shortUrl, setShortUrl] = useState("");
  const [created, setCreated] = useState(false)
  const [isValidUrl, setIsValidUrl] = useState(true);
  const [registeredUrl, setRegisteredUrl] = useState(null);
  const [loading, setLoading] = useState(false);
  const [img, setImg] = useState(null);
  const baseUrl = 'https://www.brif.live/';
  const localhost = 'http://localhost:5009'


  const handleButtonClick = async (url) => {

    url = url.toLowerCase();

    if(!validator.isURL(url)){
      return setIsValidUrl(false);
    }


    if(url.slice(0,7) !== "http://" && url.slice(0,8) !== "https://"){
        url = `https://${url}`;
    }

    setLoading(true);
    const response = await fetch(baseUrl, {
      method: "POST",
      body: JSON.stringify({
        url: url
      }),
      headers: { 'Content-Type': 'application/json' }
    });
    const data = await response.json();
    if(data.favIcon){
      setImg(data.favIcon);
    }
    setLoading(false);
    setShortUrl(`${data.shortUrl}`);
    setRegisteredUrl(`${data.registeredUrl}`)
    setCreated(true);
    setIsValidUrl(true);
  }

  const handleInputChange = (e) => {
    setOriginalUrl(e.target.value);
    if(e.target.value === ''){
      setIsValidUrl(true)
    }
  }


  const handleCreateAnother = () => {
    setOriginalUrl('');
    setShortUrl('');
    setImg(null)
    setCreated(false);
  }


  return (
    <div className={classes.App}>
      <section className={classes.Section}>
        <Form 
        originalUrl={originalUrl}
        shortenedUrl={shortUrl}
        registeredUrl={registeredUrl}
        onChange={handleInputChange}
        created={created}
        url={originalUrl}
        isValidUrl={isValidUrl}
        loading={loading}
        onClick={!created ? () => handleButtonClick(originalUrl) : handleCreateAnother}
        img={img}
        />
      </section>
    </div>
  );
}

export default App;
