import React from 'react'
import classes from './form.module.css'
import { css } from "@emotion/react";
import ClipLoader from "react-spinners/ClipLoader";


const form = props => {

    const override = css`
        margin: 0 0 64px 0;
        border-color: #4361ee;
`;

    return (
        <div className={classes.Form}>
            <h1 className={classes.Title}>Create your shortened URLs in seconds</h1>
            <p className={classes.Description}>Instantly get your shortened urls to make your life easier.</p>
            <input
                className={classes.Input}
                value={props.originalUrl}
                placeholder={"Enter a long URL"}
                disabled={props.created}
                onChange={props.onChange} />
            <p
                style={{
                    fontSize: '0.8em',
                    color: 'tomato',
                    width: '100%',
                    marginBottom: '56px',
                    paddingLeft: '6px',
                    userSelect: 'none'
                }}
            >{!props.isValidUrl ? 'Url is not valid' : ''}</p>
            {
            !props.created && !props.loading? 
            <p className={classes.UrlPlaceholder}>Your shortened URL will appear here...</p> :
            props.loading ?         
            <ClipLoader  css={override} size={55} /> :
            <div className={classes.Link}>
                {props.img ? <img alt={"fav.ico"} className={classes.Image} src={props.img} /> : null}
                <a rel={"noreferrer"} href={props.registeredUrl} target={'_blank'} className={classes.ShortenedUrl}>{props.shortenedUrl}</a>
            </div>
            }
            <button onClick={() => props.onClick(props.url)} className={classes.Button}>{!props.created ? 'Create URL' : 'Create Another'}</button>
        </div>
    )
}

export default form;
